import Vue from "vue";
import VueRouter from "vue-router";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    redirect: "/home",
  },
  {
    path: "/home",
    name: "Home",
    redirect: "/home/index",
    component: () => import("../view/home.vue"),
    children: [
      {
        path: "index",
        name: "Index",
        component: () => import("../view/index.vue"),
      },
      {
        path: "judge",
        name: "Judge",
        component: () => import("../view/judge.vue"),
      },
      {
        path: "login",
        name: "Login",
        component: () => import("../view/login.vue"),
      },
      {
        path: "handler",
        name: "Handler",
        component: ()=> import("../view/handler.vue")
      },
      {
        path: 'map',
        name: 'Map',
        component: ()=> import("../view/map.vue")
      }
    ],
  },
];

const router = new VueRouter({
  mode: "hash",
  base: process.env.BASE_URL,
  routes,
});

export default router;
