import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    loginState: false,
    normalState: false,
    mapState: {}
  },
  mutations: {
    setLoginState(state) {
      state.loginState = true
    },
    setMapState(state,payload) {
      state.mapState[payload.url] = payload.tude
    },
    setNormalState(state) {
      state.normalState = true
    }
  },
  actions: {
  },
  modules: {
  }
})
